@import url(https://fonts.googleapis.com/css?family=Josefin+Sans:400,600,700&display=swap);
article {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5%; }

h1 {
  text-align: center;
  margin: 0px 10px 30px 10px;
  padding: 10px;
  text-transform: uppercase;
  font-size: 36px;
  text-shadow: 2px 2px grey;
  box-shadow: 0px 5px lightgrey; }

.buttons-container {
  display: flex;
  justify-content: space-around;
  width: 45%; }

button {
  width: 25%;
  height: 55px;
  margin: 5px;
  font-size: 12px;
  background: white;
  border: none;
  border-top: 1px dotted lightgray;
  border-left: 1px dotted lightgray;
  box-shadow: 5px 5px black; }

#page-numbers {
  list-style: none;
  display: flex; }

#page-numbers li {
  margin: 0.5em;
  color: blue;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer; }

.clicked {
  color: salmon !important; }

@media (max-width: 768px) {
  h1 {
    margin: 20px 10px;
    font-size: 22px; }
  .buttons-container {
    width: 85%; }
  button {
    width: 100%; } }

#list-total-companies li {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  border-top: 1px dotted lightgray;
  border-left: 1px dotted lightgray;
  margin: 10px;
  padding: 5px;
  box-shadow: 5px 5px darkgray; }

img {
  position: absolute;
  align-self: flex-end;
  max-width: 110px;
  width: 20%;
  padding: 20px; }

p {
  width: 75%;
  margin: 0; }

aside {
  display: flex;
  flex-direction: column;
  margin: 10px 5px;
  font-size: 12px; }

@media (max-width: 768px) {
  p {
    width: 70%; }
  img {
    position: absolute;
    align-self: flex-end;
    max-width: 110px;
    width: 20%;
    padding: 10px; } }

ul {
  margin: 0;
  padding: 0; }

footer {
  display: flex;
  justify-content: center; }

.list-container {
  width: 70%;
  height: 100%; }

#total-companies {
  margin: 10px;
  font-weight: 700; }

@media (max-width: 768px) {
  .list-container {
    width: 100%;
    height: 100%; } }

body {
  margin: 0;
  font-family: 'Josefin Sans', "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

